import React, { useState } from 'react';
import { useSupabase } from '../contexts/SupabaseContext';
import { useTheme } from '../contexts/ThemeContext';
import { getThemeClasses } from '../themes/themeConfig';
import { useToast } from './ToastContext';
import supabase from '../lib/supabase';

export default function AuthModalUI() {
  const { signUp, signIn, signInWithGoogle } = useSupabase();
  const { currentTheme } = useTheme();
  const [mode, setMode] = useState('signIn');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [username, setUsername] = useState('');
  const [message, setMessage] = useState(null);
  const [submitting, setSubmitting] = useState(false);
  const { addToast } = useToast();
  const [usernameAvailable, setUsernameAvailable] = useState(true);

  function mapAuthErrorToMessage(err) {
    const msg = err?.message || String(err);
    if (/invalid password|invalid login|invalid_credentials|wrong password/i.test(msg)) return 'That email or password doesn\'t match our records.';
    if (/email not confirmed|email_not_confirmed/i.test(msg)) return 'Almost there  please confirm the link we sent to your email.';
    if (/user already registered|already registered|duplicate/i.test(msg)) return 'Looks like that email is already registered. Try signing in.';
    if (/invalid email|email address invalid/i.test(msg)) return 'Hmm  that email looks invalid. Try again.';
    if (/password must/i.test(msg)) return 'Your password doesn\'t meet our security requirements.';
    return 'Authentication failed. Please try again.';
  }

  function mapDbErrorToMessage(err) {
    const msg = err?.message || String(err);
    if (/duplicate key value violates unique constraint.*username|uq_profiles_username|username.*already exists/i.test(msg)) return 'That username is taken  try another.';
    if (/duplicate key value violates unique constraint.*email|uq_profiles_email|email.*already exists/i.test(msg)) return 'That email is already in use. Try signing in instead.';
    return 'Could not save profile  please try again.';
  }

  const containerClass = `${getThemeClasses(currentTheme, 'container')} p-6`;
  const inputClass = `${getThemeClasses(currentTheme, 'input')} w-full p-3 rounded-md border ${getThemeClasses(currentTheme, 'border')}`;
  const labelClass = `${getThemeClasses(currentTheme, 'label')} text-sm block mb-1`;
  const actionBtnBase = `${getThemeClasses(currentTheme, 'actionButton')} `;
  const actionBtnEnabled = `${getThemeClasses(currentTheme, 'actionButtonEnabled')}`;

  const handleSignUp = async (e) => {
    e.preventDefault();
    setMessage(null);
    setSubmitting(true);
    try {
      if (!username || username.length < 2) {
        const msg = 'Pick a short, memorable username (2+ chars).';
        setMessage(msg);
        addToast(msg, 'error');
        setSubmitting(false);
        return;
      }

      // Pre-check username availability
      try {
        const { data: existing, error: qErr } = await supabase
          .from('profiles')
          .select('id')
          .eq('username', username)
          .limit(1)
          .maybeSingle();
        if (qErr) console.warn('username check error', qErr);
        if (existing) {
          setUsernameAvailable(false);
          const msg = 'That username is already taken  try another.';
          setMessage(msg);
          addToast(msg, 'error');
          setSubmitting(false);
          return;
        }
      } catch (chkErr) {
        console.warn('username pre-check failed', chkErr);
        addToast('Could not verify username availability  attempting sign-up.', 'info');
      }

      const { error, profileError } = await signUp({ email, password, username });
      if (error) {
        const msg = mapAuthErrorToMessage(error);
        setMessage(msg);
        addToast(msg, 'error');
      } else if (profileError) {
        const dbMsg = mapDbErrorToMessage(profileError);
        setMessage(dbMsg);
        addToast(dbMsg, 'error');
      } else {
        const ok = 'Welcome to GigaSwap  account created! Check your email if confirmation is required.';
        setMessage(ok);
        addToast(ok, 'success');
        setEmail(''); setPassword(''); setUsername('');
      }
    } finally {
      setSubmitting(false);
    }
  };

  const handleSignIn = async (e) => {
    e.preventDefault();
    setMessage(null);
    setSubmitting(true);
    try {
      const { error } = await signIn({ email, password });
      if (error) {
        const m = mapAuthErrorToMessage(error);
        setMessage(m);
        addToast(m, 'error');
      } else {
        const ok = 'Signed in  welcome back!';
        setMessage(ok);
        addToast(ok, 'success');
      }
    } finally {
      setSubmitting(false);
    }
  };

  const container = (
    <div className={containerClass} role="form" aria-label={mode === 'signIn' ? 'Sign in form' : 'Sign up form'}>
      <div className="flex items-center justify-between mb-4">
        <h3 className={`${getThemeClasses(currentTheme, 'header')} text-lg`}>{mode === 'signIn' ? 'Sign in' : 'Create account'}</h3>
        <div>
          <button onClick={() => setMode('signIn')} aria-pressed={mode === 'signIn'} className={`mr-2 px-3 py-1 rounded ${mode === 'signIn' ? 'opacity-90' : 'opacity-60'}`}>Sign in</button>
          <button onClick={() => setMode('signUp')} aria-pressed={mode === 'signUp'} className={`px-3 py-1 rounded ${mode === 'signUp' ? 'opacity-90' : 'opacity-60'}`}>Sign up</button>
        </div>
      </div>

      {mode === 'signUp' ? (
        <form onSubmit={handleSignUp}>
          <div className="mb-3">
            <label className={labelClass} htmlFor="signup-email">Email</label>
            <input id="signup-email" autoFocus required type="email" className={inputClass} value={email} onChange={e => setEmail(e.target.value)} />
          </div>
          <div className="mb-3">
            <label className={labelClass} htmlFor="signup-password">Password</label>
            <input id="signup-password" required type="password" className={inputClass} value={password} onChange={e => setPassword(e.target.value)} />
          </div>
          <div className="mb-3">
            <label className={labelClass} htmlFor="signup-username">Username</label>
            <input id="signup-username" required className={inputClass} value={username} onChange={e => setUsername(e.target.value)} />
          </div>
          <div className="mt-4">
            <button type="submit" disabled={submitting} className={`${actionBtnBase} ${actionBtnEnabled} rounded-md px-4 py-2`}>{submitting ? 'Please wait...' : 'Create account'}</button>
          </div>
        </form>
      ) : (
        <form onSubmit={handleSignIn}>
          <div className="mb-3">
            <label className={labelClass} htmlFor="signin-email">Email</label>
            <input id="signin-email" autoFocus required type="email" className={inputClass} value={email} onChange={e => setEmail(e.target.value)} />
          </div>
          <div className="mb-3">
            <label className={labelClass} htmlFor="signin-password">Password</label>
            <input id="signin-password" required type="password" className={inputClass} value={password} onChange={e => setPassword(e.target.value)} />
          </div>
          <div className="mt-4">
            <button type="submit" disabled={submitting} className={`${actionBtnBase} ${actionBtnEnabled} rounded-md px-4 py-2`}>{submitting ? 'Please wait...' : 'Sign in'}</button>
          </div>
        </form>
      )}

      <div className="mt-4">
        <button onClick={() => signInWithGoogle()} className={`w-full py-2 rounded-md ${getThemeClasses(currentTheme, 'buttonSecondary')} ${getThemeClasses(currentTheme, 'textPrimary')}`}>Sign in with Google</button>
      </div>

      <div role="status" aria-live="polite" className="mt-3">
        {message && <div className={`${getThemeClasses(currentTheme, 'textSecondary')}`}>{message}</div>}
      </div>
    </div>
  )

  return container;
}
